* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell,
    'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

img {
  display: block;
  max-width: 100%;
}

/* задача 1 */
.conteiner {
  margin: 0 auto;
  text-align: center;
  padding-top: 150px;
}
.button-switcher {
  padding: 20px 30px;
  align-items: center;
  font-size: 16px;
  font-weight: 700;
  text-transform: uppercase;
}

/* задача 2 */
.timer {
  display: flex;
  justify-content: flex-start;
}
.field {
  display: block;
  margin-right: 10px;
  text-transform: uppercase;
}
.value {
  margin-top: 10px;
  display: block;
  text-align: center;
  font-size: xx-large;
}
.label {
  font-weight: 500;
}
